<template>
	<div>
		<b-btn @click="start" variant="primary" class="randomButton">Random again</b-btn>
		<b-container>
			<h3 class="mb-2">You will drink: {{result}}</h3>
			<b-form-row>
				<transition-group name="list" tag="div">
					<b-col lg="3" v-for="(element, key) in elements" :key="element.id" class="list-complete-item mb-2">
						<b-card :header-text-variant="headeTextVariant(key)" :header-bg-variant="headeBgVariant(key)" :header="element.name" :class="{'border-primary shadow': isActive(key)}" style="height: 300px;"><img :src="element.img" height="200px"></b-card>
					</b-col>
				</transition-group>
			</b-form-row>
		</b-container>
	</div>
</template>

<script>
	import lodash from 'lodash';

export default {
    name: 'mainComponent',
	data() {
		return {
			result: '...',
			maxValue: 12,
			shuffleSteps: 5,
			chooseSteps: 5,
			selected: 0,
			elements: [
				{id: 0, img: require('../assets/img/1023249176.jpg'), name: 'Green Melissa'},
				{id: 1, img: require('../assets/img/1023249164.jpg'), name: 'Golden Ceylon'},
				{id: 2, img: require('../assets/img/1023249167.jpg'), name: 'Flying Dragon'},
				{id: 3, img: require('../assets/img/1023249169.jpg'), name: 'Earl Grey Fantasy'},
				{id: 4, img: require('../assets/img/coffe.jpg'), name: 'lavAzza'},
				{id: 5, img: require('../assets/img/1023249173.jpg'), name: 'Rich Camomile'},
				{id: 6, img: require('../assets/img/1023249174.jpg'), name: 'Summer Bouquet'},
				{id: 7, img: require('../assets/img/1023249180.jpg'), name: 'Spring Melody'},
				{id: 8, img: require('../assets/img/1023249181.jpg'), name: 'Lemon Spark'},
				{id: 9, img: require('../assets/img/1023249185.jpg'), name: 'Strawberry Gourmet'},
				{id: 10, img: require('../assets/img/1023353327.jpg'), name: 'Barberry Garden'},
				{id: 11, img: require('../assets/img/1023249172.jpg'), name: 'Milky Oolong'},
			]
		}
	},
    props: {
        msg: String
    },
    mounted() {
		this.start();
	},
	methods: {
		start() {
			this.result = '...';
			this.selected = -1;
			this.shuffleRandom();
		},
		sleep(ms) {
			return new Promise(resolve => setTimeout(resolve, ms));
		},
		random() {
			return Math.floor(Math.random() * Math.floor(this.maxValue));
		},
		setActive() {
			this.selected = this.random();
			this.result = this.elements[this.selected].name;
			// this.result = this.elements;
		},
		isActive(id) {
			return this.selected === id;
		},
		headeBgVariant(id) {
			if (this.selected === id) {
				return 'primary'
			}

			return 'white';
		},
		headeTextVariant(id) {
			if (this.selected === id) {
				return 'white'
			}

			return 'dark';
		},
		shuffle: function () {
			this.elements = lodash.shuffle(this.elements);
		},
		shuffleRandom: function(count = 1) {
			this.shuffle();
			this.sleep(1100).then(() => {
				if (count !== this.shuffleSteps) {
					this.shuffleRandom(++count);
				} else {
					this.setActive();
				}
			});
		},
		// selectRandom: function(count = 1) {
		//
		// 	// this.sleep(1000).then(() => {
		// 	// 	if (count !== this.chooseSteps) {
		// 	// 		this.selectRandom(++count);
		// 	// 	}
		// 	// });
		// }
	}
}
</script>

<style scoped>
	.randomButton {
		position: absolute;
		left: 10px;
		top: 10px;
	}
	.list-complete-item {
		transition: all 1s;
		display: inline-block;
	}
	.list-complete-enter, .list-complete-leave-to
		/* .list-complete-leave-active до версии 2.1.8 */ {
		opacity: 0;
		transform: translateY(30px);
	}
	.list-complete-leave-active {
		position: absolute;
	}
</style>
